/* src/components/AboutUs.css */
.aboutus-section {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Segoe UI', sans-serif;
}

.aboutus-section h2 {
  font-size: 2.2rem;
  text-align: center;
  margin-bottom: -2px;
  color: #222;
}

.aboutus-text {
  font-size: 1.2rem;
  line-height: 1.8;
  margin-bottom: 20px;
  color: #333;
  text-align: justify; /* ✅ Justificación del texto */
}

/* Estilo separado para la sección del equipo */
.aboutus-team {
  text-align: center; /* ✅ Centrado de todo el bloque */
  margin-top: 40px;
}

.aboutus-team h3 {
  font-size: 1.6rem;
  margin-bottom: 15px;
}

.aboutus-team ul {
  list-style: none;
  padding: 0;
}

.aboutus-team li {
  font-size: 1.1rem;
  margin-bottom: 10px;
}
